import { SET_TITLE, FLASH_MESSAGE, WEB3_STATUS } from '../constants/App'
//import { web3js } from '../helpers/web3'

export function setTitle(title) {
    return {
        type: SET_TITLE,
        payload: title
    }
}

export function flashMessage(message) {
    return {
        type: FLASH_MESSAGE,
        payload: message
    }
}

export function flashMessageHide() {
    return {
        type: FLASH_MESSAGE,
        payload: false
    }
}

export function web3Status() {
    var web3 = true;
    // var web3 = false;
    // try {
    //     web3 = web3js();
    // } catch (e) {
    //     console.log(e.message);
    // }

    return {
        type: WEB3_STATUS,
        payload: web3
    }
}

