import React, { PropTypes, Component } from 'react'
import { reduxForm } from 'redux-form'

/*const validate = values => {
    const errors = {};
    if (!values.from) {
        errors.from = 'required'
    }
    if (!values.to) {
        errors.to = 'required'
    }
    if (!values.value) {
        errors.value = 'required'
    }
    errors._error = 'full error'
    return errors
};*/

export default class ModuleForm extends Component {
    render() {
        const {
            fields,
            handleSubmit,
            error,
            submitting,
            name,
            description,
            params
        } = this.props
        //const { name } = this.props

        return <div className="panel panel-default">
            <div className="panel-heading"> "{name}" {description}</div>
            <div className="panel-body">
                <form className="form-horizontal" onSubmit={handleSubmit}>
                    {Object.keys(fields).map((name, index) => {
                        const field = fields[name]
                        return (
                            <div key={index} className="form-group">
                                <label className="col-sm-5 control-label">{params[index]}</label>
                                <div className="col-sm-7">
                                    <input type="text" className="form-control" placeholder={params[index]} {...field} />
                                </div>
                                {field.touched && field.error ? field.error : ''}
                            </div>
                        )
                    })}
                    <div className="form-group">
                        <div className="text-center">
                            <input
                                type='submit'
                                className="btn btn-default"
                                disabled={submitting}
                                value={submitting ? '...' : ''}
                                />
                        </div>
                    </div>
                    {error && <div>{error}</div>}
                </form>
            </div>
        </div>
    }
}

ModuleForm.propTypes = {
    fields: PropTypes.object.isRequired,
    handleSubmit: PropTypes.func.isRequired,
    error: PropTypes.string,
    submitting: PropTypes.bool.isRequired,
    name: PropTypes.string.isRequired,
    description: PropTypes.string,
    params: PropTypes.array.isRequired
}
ModuleForm.defaultProps = {
    description: ''
}

function mapStateToProps(state, props) {
    return {
        initialValues: props.data
        // initialValues: state.dataForm
        // accounts: state.accounts.items
    }
}
export default reduxForm({
    form: 'ModuleForm'/*,
    validate*/
}, mapStateToProps)(ModuleForm)



