import { SET_TITLE, FLASH_MESSAGE, WEB3_STATUS } from '../constants/App'

const initialState = {
    title: 'React ethereum',
    web3_status: false,
    flash_message: false
}

export default function app(state = initialState, action) {
    switch (action.type) {
		case WEB3_STATUS:
			return { ...state, web3_status: action.payload}
            
		case SET_TITLE:
			return { ...state, title: action.payload}

		case FLASH_MESSAGE:
			return { ...state, flash_message: action.payload}

        default:
			return state;
    }
}



