var Promise = require('es6-promise').Promise;
var _ = require('lodash')
//var prompt = require('prompt');
var axios = require('axios');

import { web3js } from './web3'

var web3 = web3js()

//prompt.start();

export function init(json) {
    var abi_names = ['Core', 'BuilderDAO', 'BuilderTokenEther', 'BuilderShareSale'];
    loadAbis(abi_names).
        then((results)=>{
            var abis = _.reduce(abi_names, function(result, value, index) {
                return _.set(result, value, results[index].data);
            }, {});
            install(json, abis);
        }).
        catch(function(e) {
            console.log('dao', e);
        });
}

function install(json, abis) {
    var modules = ['Aira BuilderTokenEther', 'Aira BuilderShareSale']

    var params = getParamsByAbi(abis['BuilderDAO'])
    promtArgs(params).
        then(args => {
            //var addr = factory.getModule(json.core);
            var addr = 'asd';
            var builder = getContract(abis['BuilderDAO'], addr);
            return createModule(args, builder);
        }).
        then(result => {
            console.log('dao addr', result);

            return modules.reduce((promiseChain, item) => {
                return promiseChain.then(() => new Promise((resolve) => {
					var module_name = '';
                    if (_.isObject(item)) {
                        module_name = item.name
                        /*if (isset(params_link)) {

                        }*/
                    } else {
                        module_name = item
                    }
                    var module_abi = abis['BuilderTokenEther'];
                    var params = getParamsByAbi(module_abi)
                    var promt_name_module = '';
                    promtArgsModule(params).
                        then(args => {
                            console.log(args)
                            promt_name_module = _.head(args);
                            args = _.drop(args);
                            //var addr = factory.getModule(module_name);
                            var addr = 'asd';
                            var builder = getContract(module_abi, addr);
                            return createModule(args, builder);
                        }).
                        then(result => {
                            console.log('module '+ module_name +' addr', result)
                            console.log('module '+ promt_name_module +' addr', result)
                            resolve();
                            //core.setModule(promt_name_module, result.address, 'abi', true, {from:web3.eth.accounts[0], gas:300000})
                        }).
                        catch(function(e) {
                            console.log('module '+ module_name +' err', e);
                        });
                }));
            }, Promise.resolve());
        }).
        then(() => {
            console.log('done')
            //modules_q.then(() => console.log('done'))
        }).
        catch(function(e) {
            console.log('dao err', e);
        });
}

function getParamsByAbi(abi) {
    var func = _.find(abi, {name: 'create'});
    return func.inputs;
}

function promtArgs(args) {
    args = _.map(args, 'name');
    return new Promise(function(resolve) {
        console.log('ok1');
		document.getElementById('asd').onclick = function(){
			resolve([]);
		}

        //prompt.get(args, function (err, result) {
            //
            // Log the results.
            //
            /*console.log('Command-line input received:');
            console.log('  username: ' + result.username);
            console.log('  email: ' + result.email);*/
            //resolve(result);
        //});

        /*setTimeout(function(){
            resolve([]);
        }, 2000);*/
    });
}

function promtArgsModule(args) {
    args = _.map(args, 'name');
    args.unshift('name module')
    return new Promise(function(resolve) {
        console.log('ok');
		document.getElementById('asd').onclick = function(){
			resolve([]);
		}
        /*prompt.get(args, function (err, result) {
            resolve(result);
        });*/
        /*setTimeout(function(){
            resolve([]);
        }, 2000);*/
    });
}

export function createModule(args, builder) {
    args = _.values(args);
    console.log('createModule', args);
    return new Promise(function(resolve, reject) {
        // console.log('builder', builder);
        // setTimeout(function(){
        //     resolve('0x111111');
        // }, 2000);

        args = args.concat([
            {
                from: web3.eth.accounts[0],
                gas:  3000000,
                value: builder.buildingCostWei()
            }
        ]);

        var e = builder.Builded({}, '', function(error, result){
            if (error) {
                reject(e);
            }
            resolve(result.args.instance);
        })

        builder.create.apply(builder, args);
    });
}

export function linkCore(core, module, address) {
    return new Promise(function(resolve) {
        core.setModule(module, address, 'abi', true, {from:web3.eth.accounts[0], gas:300000})
        resolve(address);
    });
}

function getUrlAbi(contract) {
    var url = ''
    if (contract == 'Core') {
        url = 'https://raw.githubusercontent.com/airalab/core/master/abi/modules/Core.json'
    } else if (contract == 'BuilderDAO' || contract == 'Aira BuilderDAO') {
        url = 'https://raw.githubusercontent.com/airalab/core/master/abi/builder/BuilderDAO.json'
    } else if (contract == 'BuilderTokenEther' || contract == 'Aira BuilderTokenEther') {
        url = 'https://raw.githubusercontent.com/airalab/core/master/abi/builder/BuilderTokenEther.json'
    } else if (contract == 'BuilderShareSale' || contract == 'Aira BuilderShareSale') {
        url = 'https://raw.githubusercontent.com/airalab/core/master/abi/builder/BuilderShareSale.json'
    } else if (contract == 'BuilderMarket' || contract == 'Aira BuilderMarket') {
        url = 'https://raw.githubusercontent.com/airalab/core/master/abi/builder/BuilderMarket.json'
    }
    return url;
}

function loadAbi(url) {
    return axios.get(url);
}

export function loadAbiByName(name) {
    return loadAbi(getUrlAbi(name));
}

export function loadAbis(abis) {
    var req = _.map(abis, function(item){
        return loadAbiByName(item)
    })
    return axios.all(req)
}
export function getContract(abi, address) {
    // return {
    //     abi,
    //     address
    // }
    return web3.eth.contract(abi).at(address)
}

